html {
	font-size: 0.052083333333333vw;
	.only-mo {
		display: none;
	}
	@media screen and (min-width: 1920px){
		font-size: 1px;
	}
	.only-mo {
		display: none;
		visibility: hidden;
	}
	@include pc {
		font-size: 0.06vw;
	}
	@include mobile {
		font-size: 0.266666666666667vw;
		.only-pc {
			display: none;
			visibility: hidden;
		}
		.only-mo {
			display: block;
			visibility: visible;
		}
		@include tablet {
			font-size: 0.15vw;
			.only-pc {
				display: none;
				visibility: hidden;
			}
			.only-mo {
				display: block;
				visibility: visible;
			}
		}
	}
}

body {
	font-family: "Noto Sans KR", sans-serif;
	color: $c--p1;
	line-height: 1;
	-webkit-font-smoothing: antialiased;
	&.hidden {
		overflow: hidden;
	}
}

// common
.l-col {
	@at-root .l-col, [class*="l-col--"] {
		flex: 1 1 0px;
		-ms-flex: 1 1 0px;
		max-width: 100%;
	}
	&--fit {
		flex: 0 0 auto;
		-ms-flex: 0 0 auto;
		width: auto;
	}
	@for $i from 1 to 24 {
		&--#{$i}{
			flex: 0 0 (100% * $i/24);
			-ms-flex: 0 0 (100% * $i/24);
			max-width: (100% * $i/24);
		}
	}
}

.l-row {
	@at-root .l-row, [class*="l-row--"] {
		position: relative;
		display: flex;
		display: -ms-flexbox;
		flex-wrap: wrap;
		-ms-flex-wrap: wrap;
	}
	&--reverse {
		flex-direction: row-reverse;
		-ms-flex-direction: row-reverse;
	}
	&--between {
		justify-content: space-between;
    	-ms-flex-pack: justify;
	}
	&--around{
		justify-content: space-around;
	}
	&--v-center {
		align-items: center;
		-ms-flex-align: center;
	}
	&--h-start {
		justify-content: flex-start;
		-ms-flex-pack: start;
	}
	&--h-center {
		justify-content: center;
		-ms-flex-pack: center;
	}
	&--h-end {
		justify-content: flex-end;
		-ms-flex-pack: end;
		text-align: right;
	}
}

.l-sec{
	@at-root .l-sec, [class*="l-sec--"]{
		margin-left: auto;
		margin-right: auto;
	}
	&--max-full{
		max-width: 1920px;
	}
}

