// header
.c-header {
	$self: &;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 999;
	width: 100vw;
	margin: 0;
		// mix-blend-mode: difference!important;
	&.blend {
		mix-blend-mode: difference;
	}
	opacity: 1;
    isolation: isolate;
	transition: opacity 0.5s ease, margin 0.5s ease 0.15s;
	@include ie-only {
		background-color: rgba(255, 255, 255, 0.15);
	}
	&__wrap {
		padding: 42rem 38rem 30rem 40rem;
		@include full {
			padding: 42px 38px 30px 40px;
		}
		@include mobile {
			padding: 27rem 15rem 0;
		}
	}
	&__box {
		&--logo {
			position: absolute;
			top: 33rem;
			left: 50%;
			z-index: 1000;
			transform: translate(-50%, 0) translate(9rem, 0);
			@include full {
				top: 33px;
				transform: translate(-50%, 0) translate(9px, 0)
			}
			@include mobile {
				top: 25rem;
				left: 6rem;
				transform: none;
			}
		}
		&--keyword {
			z-index: 999;
			@at-root .br-ie &{
				flex: 1 1 0px;
			}
		}
		&--btn {
			position: relative;
			z-index: 999;
			cursor: pointer;
		}
	}
	&__keyword {
		width: 238rem;
		height: 20rem;
		path {
			fill: #fff;
			@at-root .br-ie &{
				fill: #000;
			}
		}
		@include mobile {
			display: none;
		}
	}
	&__keyword-txt {
		margin : 0 1rem 0 0;
		font-family: "roslindaletext-italic";
		font-size: 19rem;
    letter-spacing: -0.4rem;
    @include full {
			margin : 0 1px 0 0;
			font-size: 19px;
      letter-spacing: -0.4px;
		}
		@include mobile {
			font-size: 15rem;
		}
	}
	&__logo-lnk {
		display: block;
  }
	&__logo {
		width: 250rem;
		height: 40rem;
		@include mobile {
			width: 168rem;
    	height: 25rem;
		}
		&--color {
			fill: #fff;
			@at-root .br-ie &{
				fill: #000;
			}
		}
	}
	&__btn {
		width: 100rem;
		height: 20rem;
		&--menu {
			opacity: 1;
			fill: #fff;
			@include mobile {
				display: none;
			}
			@at-root .br-ie &{
				fill: #000;
			}
		}
		&--close {
			position: absolute;
			top: 0;
			left: -10px;
			font-family: "ambit-semi";
			font-size: 20rem;
			letter-spacing: 0.7rem;
			opacity: 0;
			color: #fff;
			@include full {
				left: -10rem;
			}
			@include mobile {
				display: none;
			}
			@at-root .br-ie & {
				top: 3rem;
			}
			@at-root .br-win-ch &{
				top: 2rem;
			}
		}
	}
	&__btn-bar {
		fill: #fff;
		@at-root .br-ie & {
			display: none;
		}
		&--1 {
			transition: transform 0.75s;
		}
		&--2 {
			transition: transform 0.75s;
			@include mobile {
				transform: translateY(-1rem);
			}
		}
	}
	&__hamburger {
		@at-root .br-ie & {
			position: absolute;
			top: -1rem;
			right: 0;
			width: 22rem;
			height: 22rem;
			@include full {
				height: 22px;
			}
			@include mobile {
				width: 23rem;
			}
			&:before {
				content: '';
				position: absolute;
				// top: calc(50% - 4rem);
				top: 50%;
				left: 0;
				transform: translate(0, -50%) translate(0, -4rem);
				width: 100%;
				height: 2px;
				background-color: #fff;
				transition: transform 0.75s;
				@include ie-only {
					background-color: #000;
				}
			}
			&:after {
				content: '';
				position: absolute;
				// top: calc(50% + 3rem);
				top: 50%;
				left: 0;
				transform: translate(0, -50%) translate(0, 3rem);
				width: 60%;
				height: 2px;
				background-color: #fff;
				transition: transform 0.75s;
				@include ie-only {
					background-color: #000;
				}
			}
		}
	}
	// &__hamburger-bar {
	// 	display: block;
	// 	width: 100%;
	// 	height: 2px;
	// 	background-color: #fff;
	// 	transition: transform 0.75s;
	// 	&--1 {
	// 		width: 100%;
	// 		margin: 0 0 2.5rem 0;
	// 		@include full {
	// 			margin: 0 0 2.5px 0;
	// 		}
	// 	}
	// 	&--2 {
	// 		width: 60%;
	// 		margin: 2.5rem 0 0 0;
	// 		@include full {
	// 			margin: 2.5px 0 0 0;
	// 		}
	// 		@include mobile {
	// 			margin: 2rem 0 0 0;
	// 		}
	// 	}
	// }
	&__menu {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
		height: 0;
    background-color: #000;
		opacity: 0;
    transition: opacity 0.75s;
  }
  &__menu-inner {
		position: relative;
    width: 100%;
		height: 100%;
		@include mobile {
			padding: 0 24rem;
		}
	}
	&__color-box {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		z-index: 0;
	}
	&__color {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 0;
		// transition: height 0.5s cubic-bezier(0.49, 0.59, 0.33, 0.91);
		&--1 {
			background-color: #44b8f3;
		}
		&--2 {
			background-color: #ffd700;
		}
		&--3 {
			background-color: #e93ab8;
		}
		&--4 {
			background-color: #f93822;
		}
		&--5 {
			background-color: black;
		}
	}
  &__nav {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		opacity: 0;
		transition: none;
		text-indent: -9999px;
		@include mobile {
			top: 119rem;
			left: auto;
			transform: none;
		}
  }
  &__nav-itm {
		text-align: center;
		margin: 0 0 17rem 0;
		&:last-child {
			margin: 0;
		}
    &:hover {
      #{$self}__nav-lnk {
        z-index: 10;
				color: #004a33;
				@include mobile{
					color: #fff;
				}
      }
		}
		@include full {
			margin: 0 0 17px 0;
		}
		@include mobile {
			text-align: left;
			margin: 0 0 14rem 0;
		}
  }
  &__nav-lnk {
    display: inline-block;
    position: relative;
		font-family: "ambit-semi";
		font-size: 80rem;
		color: #fff;
		@include full {
			font-size: 80px;
		}
		@include mobile {
			font-size: 26rem;
		}
	}
	&__nav-name {
		font-family: "roslindaletext";
		font-size: 74rem;
		letter-spacing: -1.4rem;
		@include full {
			font-size: 74px;
			letter-spacing: -1.4px;
		}
		@include mobile {
			font-size: 25rem;
			letter-spacing: -0.4rem;
		}
	}
	&__nav-keyword {
		display: none;
		@include mobile {
			display: block;
			position: absolute;
			right: -100%;
			bottom: 16rem;
			transition: none;
			font-size: 16rem;
			#{$self}__keyword {
				display: block;
				width: 176rem;
			}
		}
	}
  &--open {
		mix-blend-mode: normal;
		#{$self}__keyword {
			path {
				@at-root .br-ie &{
					fill: #fff;
				}
			}
		}
		#{$self}__box--logo {
			display: none;
		}
		#{$self}__btn--menu{
			opacity: 0;
		}
		#{$self}__btn--close {
			opacity: 1;
			// transition: opacity 0.7s cubic-bezier(0.46, 0.03, 0.52, 0.96);
		}
		#{$self}__menu {
			height: 100%;
			opacity: 1;
		}
		#{$self}__btn-bar {
			&--1 {
				transform: translateY(10px);
				@at-root .br-ie &{
					fill: #fff;
				}
			}
			&--2 {
				transform: translateY(-17px);
				opacity: 0;
				transition: transform 0.65s, opacity 0s 0.6s;
				@at-root .br-ie &{
					fill: #fff;
				}
			}
		}
		#{$self}__hamburger {
			&:before {
				transform: translate(0, -50%) translate(0, 0);
				@include ie-only {
					background-color: #fff;
				}
			}
			&:after {
				transform: translate(0, -50%) translate(0, -1rem);
				opacity: 0;
				transition: transform 0.75s, opacity 0s 0.4s;
				@include ie-only {
					background-color: #fff;
				}
			}
		}
		#{$self}__color-box {
			bottom: 0;
		}
		#{$self}__color {
			transition: height 0.5s cubic-bezier(0.49, 0.59, 0.33, 0.91);
			&--1 {
				height: 100%;
				transition-delay: 0.5s;
			}
			&--2 {
				height: 100%;
				transition-delay: 0.55s;
			}
			&--3 {
				height: 100%;
				transition-delay: 0.6s;
			}
			&--4 {
				height: 100%;
				transition-delay: 0.65s;
			}
			&--5 {
				height: 100%;
				transition-delay: 0.7s;
			}
		}
		#{$self}__nav {
			opacity: 1;
			transition: opacity 0.85s cubic-bezier(0.51, 0.08, 0.36, 0.78) 1.1s;
			text-indent: 0;
			@include mobile {
				transition: opacity 0.8s cubic-bezier(0.51, 0.08, 0.36, 0.78) 1s;
			}
		}
		#{$self}__nav-keyword {
			@include mobile {
				right: 15rem;
				transition: right 0.8s ease 1s;
			}
		}
	}
}

