.c-footer {
	position: relative;
  display: flex;
	z-index: 100;
  height: 280rem;
  flex-direction: row-reverse;
	&__logo {
		width: 208rem;
		height: 40rem;
		@include mobile {
			width: 145rem;
    	height: 30rem;
		}
	}
	&__logo--color {
		fill: #fff;
	}
	.footer {
		&-map {
      width: 670rem;
      height: 100%;
      position: relative;
      &__link {
        display: block;
        width: 100%;
        height: 100%;
        &:hover {
          .footer-map__img {
            img {
              transform: scale(1.1);
            }
          }
        }
      }
			&__img {
				position: absolute;
				top: 0;
        left: 0;
        right: 0;
        bottom: 0;
				overflow: hidden;
				&:after {
					content: '';
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					background-color: rgba($color: #000000, $alpha: 0.5);
				}
				img {
          width: 100%;
          transition: transform 0.3s ease;
				}
			}
			&__txt {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
			}
			&__address {
				position: absolute;
				top: 27rem;
				left: 29rem;
				font-size: 14rem;
				color: #fff;
				line-height: 1.4;
				letter-spacing: -0.3rem;
				@include mobile {
					top: 14rem;
					left: 14rem;
					font-size: 12rem;
				}
			}
			&__map {
				position: absolute;
				right: 29rem;
				bottom: 29rem;
				font-size: 14rem;
				color: #fff;
				&:after {
					content: '';
					width: 100%;
					height: 1px;
					background: #fff;
					position: absolute;
					bottom: -2px;
					left: 0;
				}
				@include mobile {
					bottom: auto;
					top: 14rem;
					right: 14rem;
					font-size: 12rem;
				}
			}
    }
    &-copyright {
      position: relative;
      flex: 1;
      background: #124734;
      padding: 26rem 0 0 37rem;
      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 50%;
        width: 1px;
        height: 100%;
				background: #fff;
				opacity: 0.3;
      }
      &__logo {
				a {
					display: block;
				}
      }
      &__txt {
        margin: 165rem 0 0 0;
        font-size: 13rem;
        color: #fff;
        font-family: "ambit-regular";
        line-height: 1.23;
				text-transform: uppercase;
				@include pc {
					margin: 150rem 0 0 0;
				}
      }
      &__sns {
        position: absolute;
        color: #fff;
        top: 29rem;
        left: 50%;
        li {
          padding: 0 0 3rem 35rem;
        }
        a {
          position: relative;
					display: inline-block;
					font-family: "ambit-regular";
          font-size: 13rem;
          line-height: 1.23;
          text-transform: uppercase;
          &:after {
            position: absolute;
            left: 0;
            right: 0;
            bottom: -1px;
            transform: scaleX(0);
            transform-origin: left bottom;
            transition: transform .2s ease-in-out;
            border-bottom: 1px solid #fff;
            content: '';
						@at-root .br-ie &{
							bottom: 1px;
						}
						@at-root .br-win-ch &{
							bottom: 1px;
						}
          }
          &:hover {
            &:after {
              transform: scaleX(1);
              transition: transform .4s ease-in-out;
            }
          }
        }
      }
    }
	}
  @include mobile {
    & {
      height: auto;
			flex-direction: column;
			@include ie-only {
				height: 520rem;
			}
    }
    .footer {
      &-map {
        width: 100%;
        height: 110rem;
        @include tablet {
          height: 180rem;
        }
        &__link {
          &:hover {
            .footer-map__img {
              img {
                transform: none;
              }
            }
          }
        }
      }
      &-copyright {
				padding: 13rem 0 18rem 12rem;
				overflow: hidden;
        &::before {
          display: none;
        }
        &__txt {
          margin-top: 320rem;
					font-size: 11rem;
        }
        &__sns {
          top: 99rem;
          left: auto;
          right: 21rem;
          li {
            padding: 0 0 0 26.5rem;
            & + li {
              margin: 6rem 0 0 0;
            }
            a {
							font-size: 12rem;
							letter-spacing: -0.6rem;
              &:after {
                display: none;
              }
            }
          }
        }
      }
    }
	}
	@include tablet {
		& {
			@include ie-only {
				height: 573rem;
			}
		}
		.footer {
			&-copyright {
				&__txt {
					@include ie-only {
						margin-top: 320rem;
					}
				}
			}
		}
	}
}

